import PropTypes from 'prop-types'
import { useCallback, useContext, useEffect, useMemo, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import Icon from '../../Common/Icon'
import styles from './layer-selector.module.css'
import { stop, onKey } from '../../event-util'
import {
  selectLayers,
  createLayer,
  renameLayer,
  deleteLayer
} from './layersSlice'
import { LayoutContext } from '../../Contexts/Layout'

function LayerSelector ({ activeLayer, onSelect }) {
  const ref = useRef(null)
  const dispatch = useDispatch()
  const { bindingCount } = useContext(LayoutContext)
  const layers = useSelector(selectLayers)
  const [renaming, setRenaming] = useState(false)
  const [editing, setEditing] = useState('')

  const handleSelect = useCallback(function (layerIndex) {
    if (layerIndex === activeLayer) {
      setEditing(layers[activeLayer].name)
      setRenaming(true)
      return
    }

    setRenaming(false)
    onSelect(layerIndex)
  }, [layers, activeLayer, setEditing, setRenaming, onSelect])

  const handleAdd = useCallback(() => dispatch(createLayer({
    bindingCount
  })), [dispatch, bindingCount])

  const handleDelete = useMemo(() => function (layerIndex, layerName) {
    if (window.confirm(`Really delete layer: ${layerName}?`)) {
      dispatch(deleteLayer({ index: layerIndex }))
    }
  }, [dispatch])

  const finishEditing = useCallback(() => {
    if (!renaming) {
      return
    }

    const index = activeLayer
    const name = editing

    setEditing('')
    setRenaming(false)
    dispatch(renameLayer({ index, name }))
  }, [editing, renaming, activeLayer, setEditing, setRenaming, dispatch])

  const cancelEditing = useCallback(() => {
    if (!renaming) {
      return
    }

    setEditing('')
    setRenaming(false)
  }, [renaming, setEditing, setRenaming])

  const handleClickOutside = useMemo(() => function (event) {
    const clickedOutside = ref.current && !ref.current.contains(event.target)
    if (!clickedOutside) {
      return
    }

    cancelEditing()
  }, [ref, cancelEditing])

  useEffect(() => {
    document.addEventListener('click', handleClickOutside)
    return () => document.removeEventListener('click', handleClickOutside)
  }, [handleClickOutside])

  const focusInput = useCallback(node => {
    if (node) {
      node.focus()
      node.select()
    }
  }, [])

  return (
    <div
      className={styles.layerSelector}
      data-renaming={renaming}
      ref={ref}
    >
      <ul>
        {layers.map(({ name }, i) => (
          <li
            key={`layer-${i}`}
            className={activeLayer === i ? styles.active : ''}
            data-layer={i}
            onClick={stop(() => handleSelect(i))}
          >
            <span className={styles.index}>{i}</span>
            {(activeLayer === i && renaming)
              ? (
              <input
                ref={focusInput}
                className={styles.name}
                onChange={e => setEditing(e.target.value)}
                onKeyDown={onKey({
                  Enter: finishEditing,
                  Escape: cancelEditing
                })}
                value={
                  (activeLayer === i && renaming)
                    ? editing
                    : name
                }
              />
                )
              : (
              <span className={styles.name}>
                {name}
                <Icon
                  name="times-circle"
                  className={styles.delete}
                  onClick={stop(() => handleDelete(i, name))}
                />
              </span>
                )}
          </li>
        ))}
        <li onClick={handleAdd}>
          <Icon className={styles.index} name="plus" />
          <span className={styles.name}>Add Layer</span>
        </li>
      </ul>
    </div>
  )
}

LayerSelector.propTypes = {
  activeLayer: PropTypes.number.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default LayerSelector
