import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'

function KeymapUsingMiryoku (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Could not parse Miryoku keymap</h2>
        <p>
          It looks like your keyboard is built using Miryoku to manage the
          configuration and keymap. Parsing and editing keymaps that use this
          syntax is unsupported by the Keymap Editor.
        </p>
        <p>
          Please either continue editing your keymap by hand with these helpers
          or if you would like to use this editor, rewrite the keymap to use
          ZMK's syntax directly.
        </p>
      </DialogBox>
    </Modal>
  )
}

KeymapUsingMiryoku.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default KeymapUsingMiryoku
