import { useState } from 'react'
import PropTypes from 'prop-types'

import ActionButton from '../../../Common/ActionButton'
import DialogBox from '../../../Common/DialogBox'
import Modal from '../../../Common/Modal'

function Commit ({ onConfirm, onCancel }) {
  const [message, setMessage] = useState('')

  return (
    <Modal>
      <DialogBox dismissText={null}>
        <h2>Commit keymap changes</h2>
        <textarea
          style={{ width: '100%' }}
          placeholder='Message (optional)'
          value={message}
          onChange={event => {
            setMessage(event.target.value)
          }}
        />
        <p style={{ textAlign: 'center', gap: '10px' }}>
          <ActionButton onClick={() => onConfirm(message)}>Commit</ActionButton>
          <ActionButton onClick={onCancel} secondary>Cancel</ActionButton>
        </p>
      </DialogBox>
    </Modal>
  )
}

Commit.propTypes = {
  onConfirm: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired
}

export default Commit
