import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import KeyboardLayout from '../KeyboardLayout'
import Key from '../Keys/Key'
import { selectLayers, updateKeyBinding } from './layersSlice'

export default function LayerBindings ({ layout, activeLayer }) {
  const dispatch = useDispatch()
  const layers = useSelector(selectLayers)
  const layer = layers[activeLayer]

  const handleUpdateBind = useCallback(function (bindIndex, binding) {
    dispatch(updateKeyBinding({ layerIndex: activeLayer, bindIndex, binding }))
  }, [dispatch, activeLayer])

  return (
    <KeyboardLayout
      layout={layout}
      bindings={layer.bindings}
      renderKey={({ index, keyLayout }) => layer.bindings[index] && (
        <Key
          label={keyLayout.label}
          value={layer.bindings[index]?.value}
          params={layer.bindings[index]?.params}
          onUpdate={bind => handleUpdateBind(index, bind)}
        />
      )}
    />
  )
}
