import PropTypes from 'prop-types'

import styles from './styles.module.css'
import KeyPositionSelector from '../Common/KeyPositionSelector'

function SelectKeyCTA () {
  return (
    <p className={styles.floatingMessage}>
      Click on a key to add it to the combo
    </p>
  )
}

function ComboInputMap ({ keyPositions, onUpdate }) {
  return (
    <div className={styles.comboInputMap}>
      <KeyPositionSelector
        value={keyPositions}
        onUpdate={onUpdate}
      />
      {keyPositions.length < 2 && <SelectKeyCTA />}
    </div>
  )
}

ComboInputMap.propTypes = {
  onUpdate: PropTypes.func.isRequired,
  keyPositions: PropTypes.arrayOf(
    PropTypes.number
  ).isRequired
}

export default ComboInputMap
