import fuzzysort from 'fuzzysort'
import PropTypes from 'prop-types'
import { useCallback, useMemo } from 'react'

import ResultList from './ResultList'
import SearchFilter from './SearchFilter'
import style from './style.module.css'

function ChoicesPicker (props) {
  const { choices, prompt, onSelect } = props
  const formattedChoices = useMemo(() => {
    return choices.map(value => ({ value }))
  }, [choices])

  const renderSearchResult = useCallback(result => {
    const title = result.search
      ? (
      <span dangerouslySetInnerHTML={{
        __html: fuzzysort.highlight(result.search)
      }} />
        )
      : (
      <span>
        {result.value}
      </span>
        )

    return (
      <>
        {title}
        {result.description && <p className={style.description}>{result.description}</p>}
      </>
    )
  }, [])

  return (
    <div className={style.dialog}>
      <p>{prompt || 'Select'}</p>
      <SearchFilter
        choices={formattedChoices}
        initialQuery=""
        searchKey="value"
        renderResults={results => results && (
          <ResultList
            results={results}
            onSelect={choice => onSelect(choice.value)}
            renderResult={renderSearchResult}
          />
        )}
      />
    </div>
  )
}

ChoicesPicker.propTypes = {
  choices: PropTypes.arrayOf(PropTypes.stirng).isRequired,
  prompt: PropTypes.string,
  onSelect: PropTypes.func.isRequired
}

export default ChoicesPicker
