import filter from 'lodash/filter'
import get from 'lodash/get'
import isEmpty from 'lodash/isEmpty'
import keyBy from 'lodash/keyBy'
import map from 'lodash/map'
import { useContext, useMemo } from 'react'

import { DefinitionsContext } from '../Contexts/Definitions'
import { SearchContext, SettingsContext } from '../providers'
import { getMergedGlyphs } from '../keycodes'

function Context ({ children, ...props }) {
  const { keymap } = props
  const { keycodes, behaviours, glyphs } = useContext(DefinitionsContext)
  const { settings } = useContext(SettingsContext)

  const mergedGlyphs = useMemo(() => {
    const isMac = window.navigator.platform.startsWith('Mac')
    const isWin = window.navigator.platform.startsWith('Win')
    const platform = (isMac && 'mac') || (isWin && 'win')

    return getMergedGlyphs(glyphs, {
      unicode: get(settings, 'unicodeGlyphs', true),
      platform: get(settings, 'platformGlyphs', 'auto') !== 'auto'
        ? settings.platformGlyphs
        : platform
    })
  }, [glyphs, settings])

  const extendedBehaviours = useMemo(() => {
    const fullList = [
      ...behaviours,
      ...(keymap.behaviors || []).map(behavior => ({
        code: `&${behavior.labels[0]}`,
        bind: `&${behavior.labels[0]}`,
        name: behavior.name,
        description: `Custom behavior: "${behavior.properties.label.raw || behavior.name}"`,
        params: behavior.params
      })),
      ...keymap.macros.map(macro => ({
        code: `&${macro.labels[0]}`,
        name: macro.name,
        description: 'Custom macro',
        params: []
      }))
    ]

    // TODO: at this point we're just duplicating work that was done at a higher
    // level. It still needs to happen here because of the extended list of
    // keyboard-specific behaviours, so the App can just pass the list without
    // doing its own indexing.
    fullList.indexed = keyBy(fullList, 'code')
    fullList.context = {
      none: fullList.filter(({ context }) => !context),
      macros: filter(fullList, { context: 'macros' })
    }
    fullList.byContext = {
      none: keyBy(fullList.context.none, 'code'),
      macros: keyBy(fullList.context.macros, 'code')
    }

    return fullList
  }, [behaviours, keymap.behaviors, keymap.macros])

  const availableLayers = useMemo(() => isEmpty(keymap)
    ? []
    : (
        keymap.layers.map((layer, i) => ({
          code: i,
          description: layer.name || `Layer ${i}`
        }))
      ), [keymap])

  // Extend ZMK definitions (keycodes and behaviours) with additional
  // definitions specific to this keyboard. This includes a list of layers for
  // layer-related behaviours and in the future can include custom behaviours
  // defined in the keymap and potentially preprocessor definitions for psuedo
  // layer names or aliased key codes.
  const sources = useMemo(() => ({
    code: keycodes.indexed,
    behaviours: extendedBehaviours.indexed,
    behavioursByContext: extendedBehaviours.byContext,
    layer: keyBy(availableLayers, 'code')
  }), [keycodes, extendedBehaviours, availableLayers])

  const searchTargets = useMemo(() => {
    return {
      behaviour: extendedBehaviours,
      code: keycodes
    }
  }, [extendedBehaviours, keycodes])

  const getSearchTargets = useMemo(() => function (param, behaviour, context = 'none') {
    // Special case for behaviour commands which can dynamically add another
    // parameter that isn't defined at the root level of the behaviour.
    // Currently this is just `&bt BT_SEL` and is only represented as an enum.
    if (param.enum) {
      return param.enum.map(v => ({ code: v }))
    }

    if (param.raw) {
      return []
    }

    if (param === 'command') {
      return get(sources.behavioursByContext, [context, behaviour, 'commands'], [])
    }

    if (!searchTargets[param]) {
      console.error('cannot find target for', param)
    }

    if (param === 'behaviour') {
      return searchTargets.behaviour.context[context]
    }

    return searchTargets[param]
  }, [searchTargets, sources])

  const identifiers = useMemo(() => {
    return [
      ...map(keycodes, 'code'),
      ...Object.keys(keymap.defines || {})
    ]
  }, [keycodes, keymap.defines])

  return (
    <SearchContext.Provider value={{ getSearchTargets, mergedGlyphs, sources, identifiers }}>
      {children}
    </SearchContext.Provider>
  )
}

export default Context
