import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Icon from '../Common/Icon'
import Modal from '../Common/Modal'

const styles = {
  description: {
    fontStyle: 'italic',
    fontSize: '90%'
  }
}

function Settings (props) {
  const { settings, onUpdate, onClose } = props

  function handleUpdateSetting (key, value) {
    onUpdate({ ...settings, [key]: value })
  }

  return (
    <Modal onDismiss={onClose}>
      <DialogBox onDismiss={onClose}>
        <h1>Settings</h1>

        <h2>Keycode glyphs</h2>

        <h3>
          <label htmlFor="enable-unicode-glyphs">
            <input
              id="enable-unicode-glyphs"
              type="checkbox"
              checked={settings.unicodeGlyphs}
              onChange={e => (
                handleUpdateSetting('unicodeGlyphs', e.target.checked)
              )}
            />
            Use Unicode Glyphs
          </label>
        </h3>
        <p style={styles.description}>
          Use unicode glyphs for visual representations of keycodes
          like <code>Enter</code> (), <code>Right Arrow</code> (), etc. You
          can disable this if the symbols don't render correctly.
        </p>

        <h3>
          <label htmlFor="platform-specific-glyphs">
            Platform specific glyphs
            <select
              id="platform-specific-glyphs"
              value={settings.platformGlyphs || 'auto'}
              onChange={e => (
                handleUpdateSetting('platformGlyphs', e.target.value)
              )}
            >
              <option value="auto">Automatic</option>
              <option value="win">Windows</option>
              <option value="mac">macOS</option>
            </select>
          </label>
        </h3>
        <p style={styles.description}>
          Render symbols specific to your platform. E.g., render left/right meta
          key as <Icon name="windows fa-brands" /> for Windows, or
           for macOS.
        </p>
      </DialogBox>
    </Modal>
  )
}

Settings.propTypes = {
  settings: PropTypes.object.isRequired,
  onUpdate: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired
}

Settings.defaultProps = {
  settings: {}
}

export default Settings
