import { useCallback, useContext, useMemo, useReducer } from 'react'
import PropTypes from 'prop-types'

import { getLayoutPolygons, getWrapperStyle } from '../util'
import KeyboardLayout from '../KeyboardLayout'
import KeyWrapper from '../Keys/KeyWrapper'

import { useDragSelector } from './DragSelector'
import styles from './keyPositionSelectorStyles.module.css'
import { compact } from 'lodash'
import Tip from '../../Common/Tip'
import { LayoutContext } from '../../Contexts/Layout'

const layoutScale = 0.2
const getPolygons = layout => {
  const polygons = getLayoutPolygons(layout, { scale: layoutScale })
  for (const polygon of polygons) {
    for (const point of polygon) {
      point.x *= layoutScale
      point.y *= layoutScale
    }
  }
  return polygons
}

function useUsageMetrics () {
  const [state, dispatch] = useReducer((state, action) => {
    switch (action) {
      case 'INCREMENT_MANUAL_SELECTION':
        return { ...state, manualSelectionCount: state.manualSelectionCount + 1 }
      case 'INCREMENT_MANUAL_DESELECTION':
        return { ...state, manualDeSelectionCount: state.manualDeSelectionCount + 1 }
      case 'TRACK_DRAG_SELECTION':
        return { ...state, usedDragSelection: true }
      case 'TRACK_DRAG_DESELECTION':
        return { ...state, usedDragDeSelection: true }
      default:
        throw new Error(`Unrecognized action type in ${action}`)
    }
  }, {
    manualSelectionCount: 0,
    manualDeSelectionCount: 0,
    usedDragSelection: false,
    usedDragDeSelection: false
  })

  const manualSelection = useCallback(() => dispatch('INCREMENT_MANUAL_SELECTION'), [dispatch])
  const manualDeSelection = useCallback(() => dispatch('INCREMENT_MANUAL_DESELECTION'), [dispatch])
  const dragSelection = useCallback(() => dispatch('TRACK_DRAG_SELECTION'), [dispatch])
  const dragDeSelection = useCallback(() => dispatch('TRACK_DRAG_DESELECTION'), [dispatch])

  return {
    showSelectTip: !state.usedDragSelection && state.manualSelectionCount > 3,
    showDeSelectTip: !state.usedDragDeSelection && state.manualDeSelectionCount > 3,
    actions: {
      manualSelection,
      manualDeSelection,
      dragSelection,
      dragDeSelection
    }
  }
}

function KeyPositionSelector ({ value, prompt, onUpdate }) {
  const { layout, wrapperDimensions } = useContext(LayoutContext)
  const keyPolygons = useMemo(() => getPolygons(layout), [layout])
  const wrapperStyle = useMemo(() => getWrapperStyle(wrapperDimensions, {
    scale: layoutScale,
    overrides: {
      position: 'relative',
      padding: 0,
      margin: 0
    }
  }), [wrapperDimensions])

  const {
    showSelectTip,
    showDeSelectTip,
    actions: selectionActions
  } = useUsageMetrics()

  const handleDragSelect = useCallback(({ negate, intersecting }) => {
    negate ? selectionActions.dragDeSelection() : selectionActions.dragSelection()
    onUpdate(
      negate
        ? value.filter(i => !intersecting.includes(i))
        : [...value, ...intersecting]
    )
  }, [value, onUpdate, selectionActions])

  const handleSelectKey = useCallback(index => {
    const isSelected = value.includes(index)
    isSelected ? selectionActions.manualDeSelection() : selectionActions.manualSelection()

    onUpdate(
      isSelected
        ? value.filter(keyPosition => keyPosition !== index)
        : [...value, index]
    )
  }, [value, onUpdate, selectionActions])

  const [dragProps, DragSelectContainer] = useDragSelector({
    onSelect: handleDragSelect,
    polygons: keyPolygons
  })

  const intersecting = dragProps.selecting
    ? dragProps.intersecting
    : []

  const selectedIndices = value
  const previewAddingIndices = dragProps.negate ? [] : intersecting.filter(index => !selectedIndices.includes(index))
  const previewRemovingIndices = dragProps.negate ? intersecting.filter(index => selectedIndices.includes(index)) : []

  return (
    <div style={wrapperStyle}>
      <DragSelectContainer {...dragProps}>
        <KeyboardLayout
          layout={layout}
          bindings={[]}
          scale={layoutScale}
          renderKey={({ index }) => (
            <KeyWrapper
              onClick={() => handleSelectKey(index)}
              themeClass={compact([
                styles.key,
                selectedIndices.includes(index) && styles.highlight,
                previewAddingIndices.includes(index) && styles.previewAdd,
                previewRemovingIndices.includes(index) && styles.previewRemove
              ]).join(' ')}
            />
          )}
        />
        {prompt && <prompt />}
        {showSelectTip && <Tip className={styles.tip}>Drag to select a region</Tip>}
        {showDeSelectTip && <Tip className={styles.tip}>Shift+Drag to de-select a region</Tip>}
      </DragSelectContainer>
    </div>
  )
}

KeyPositionSelector.propTypes = {
  onUpdate: PropTypes.func.isRequired,
  value: PropTypes.arrayOf(
    PropTypes.number
  ).isRequired,
  prompt: PropTypes.node
}

export default KeyPositionSelector
