import { useCallback, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import ActionButton from '../../Common/ActionButton'
import Icon from '../../Common/Icon'
import BehaviorEditor from './BehaviorEditor'
import BetaNoticeDialog from './BetaNoticeDialog'
import NewBehaviorModal from './NewBehaviorModal'
import styles from './styles.module.css'
import { createBehavior } from './create-behavior'
import {
  selectBehaviors,
  addBehavior,
  deleteBehavior,
  updateBehavior
} from './behaviorsSlice'

function Behaviors () {
  const experimentalSettings = useSelector(state => state.experimentalSettings)

  const [editing, setEditing] = useState(null)
  const [creating, setCreating] = useState(false)
  const [betaDisclaimerVisible, setBetaDisclaimerVisible] = useState(false)
  const behaviors = useSelector(selectBehaviors)
  const dispatch = useDispatch()

  const handleAdd = useCallback(() => {
    setCreating(true)
  }, [setCreating])

  const handleAddPending = useCallback(compatible => {
    setCreating(false)
    dispatch(addBehavior({
      behavior: createBehavior(compatible, {
        name: 'new_behavior',
        labels: ['new_behavior'],
        label: 'NEW_BEHAVIOR'
      })
    }))

    setTimeout(() => setEditing(behaviors.length))
  }, [behaviors, dispatch, setCreating, setEditing])

  const handleEdit = useCallback(index => {
    setEditing(index)
  }, [setEditing])

  const handleDelete = useCallback((index) => {
    dispatch(deleteBehavior({ behaviorIndex: index }))
  }, [dispatch])

  const showBetaDialog = useCallback(event => {
    event.stopPropagation()
    setBetaDisclaimerVisible(true)
  }, [setBetaDisclaimerVisible])

  const hideBetaDialog = useCallback(() => {
    setBetaDisclaimerVisible(false)
  }, [setBetaDisclaimerVisible])

  const toggleBeta = useCallback(enableBehaviorEditing => {
    dispatch({
      type: 'SETTINGS_UPDATE',
      settings: {
        ...experimentalSettings,
        enableBehaviorEditing
      }
    })
  }, [experimentalSettings, dispatch])

  return (
    <div className={styles.main}>

      {creating && (
        <NewBehaviorModal
          onUpdate={handleAddPending}
          onCancel={() => setCreating(false)}
        />
      )}

      {editing !== null && (
        <BehaviorEditor
          node={behaviors[editing]}
          onCancel={() => setEditing(null)}
          onUpdate={behavior => {
            setEditing(null)
            dispatch(updateBehavior({
              behaviorIndex: editing,
              behavior
            }))
          }}
        />
      )}

      {betaDisclaimerVisible && (
        <BetaNoticeDialog onDismiss={hideBetaDialog} />
      )}

      <div className={styles.panel}>
        <label>
          Enable Beta
          <input
            type="checkbox"
            checked={experimentalSettings.enableBehaviorEditing}
            onChange={e => toggleBeta(e.target.checked)}
          />
        </label>

        <ActionButton onClick={showBetaDialog} style={{ backgroundColor: 'orange' }}>
          <Icon name="warning" /> Details
        </ActionButton>

        <h3>Custom Behaviors</h3>
        <table style={{ width: '100%' }}>
          <thead>
            <tr>
              <th>Binding</th>
              <th>Compatible</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {behaviors.map((behavior, i) => (
              <tr key={i}>
                <td><code>&amp;{behavior.labels[0]}</code></td>
                <td>{behavior.properties.compatible.raw}</td>
                <td>
                  {experimentalSettings.enableBehaviorEditing && (
                    <>
                      <Icon className={styles.action} onClick={() => handleEdit(i)} name="wrench" />
                      <Icon className={styles.action} onClick={() => handleDelete(i)} name="trash-can" />
                    </>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        <ActionButton onClick={handleAdd}>
          Add New Behavior
        </ActionButton>
      </div>
    </div>
  )
}

export default Behaviors
