import * as config from './config'

export function healthcheck () {
  return fetch(`${config.apiBaseUrl}/health`)
}

export async function loadBehaviourBindings () {
  const { behaviorSchemas, behaviorBindings } = await import('zmk-data/behaviors')
  return {
    behaviors: behaviorSchemas,
    bindings: behaviorBindings
  }
}

export async function loadKeycodes () {
  const { keycodes } = await import('zmk-data/keycodes')
  return keycodes
}

export async function loadKeycodeGlyphs () {
  const { keycodeGlyphs } = await import('zmk-data/keycodes')
  return keycodeGlyphs
}

export async function loadAvailableKeyboards () {
  const response = await fetch(`${config.apiBaseUrl}/v2/local/keyboards`)
  return response.json()
}

export async function loadKeyboardFiles (keyboard) {
  const path = `v2/local/keyboards/${encodeURIComponent(keyboard)}`
  const url = `${config.apiBaseUrl}/${path}`
  const response = await fetch(url)

  if (!response.ok) {
    const err = new Error()
    err.response = response
    throw err
  }
  return response.json()
}

export async function updateKeymap (keyboard, keymap) {
  const path = `v2/local/keyboards/${encodeURIComponent(keyboard)}/keymap`
  const url = `${config.apiBaseUrl}/${path}`

  const response = await fetch(url, {
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(keymap)
  })

  return response.json()
}

export async function loadKeyboard () {
  const response = await fetch(`${config.apiBaseUrl}/keyboard`)
  const { info, keymap } = await response.json()
  const defaultLayout = (
    info.layouts.default ||
    info.layouts[Object.keys(info.layouts)[0]]
  )

  return {
    layout: defaultLayout.layout,
    keymap,
    sensors: info.sensors
  }
}

export function loadKeymap () {
  const url = new URL(`${config.apiBaseUrl}/keymap`)
  return fetch(url)
    .then(response => response.json())
}

export function loadLayout () {
  return fetch(`${config.apiBaseUrl}/layout`)
    .then(response => response.json())
}
