import moment from 'moment'
import PropTypes from 'prop-types'
import { useEffect, useState } from 'react'

import Icon from '../../Common/Icon'
import Link from '../../Common/Link'
import Spinner from '../../Common/Spinner'
import styles from './styles.module.css'

const goodConclusions = [
  'success'
]

const badConclusions = [
  'action_required',
  'cancelled',
  'failure',
  'skipped',
  'stale',
  'timed_out'
]

function FuzzyTimestamp (props) {
  const { timestamp } = props
  const [fuzzy, setFuzzy] = useState(
    moment(timestamp).fromNow()
  )

  useEffect(() => {
    setFuzzy(moment(timestamp).fromNow())
    const interval = setInterval(() => {
      setFuzzy(moment(timestamp).fromNow())
    }, 60 * 1000)

    return () => clearInterval(interval)
  }, [timestamp, setFuzzy])

  return (
    <span title={timestamp.toISOString()}>
      {fuzzy}
    </span>
  )
}

function BuildLink (props) {
  const { latestRun } = props
  const mappedIcon = goodConclusions.includes(latestRun.conclusion)
    ? <Icon name="check" />
    : badConclusions.includes(latestRun.conclusion)
      ? <Icon name="xmark" />
      : <Spinner />

  return (
    <Link
      href={latestRun.html_url}
      className={styles.buildDetails}
      external={true}
      showExternal={false}
    >
      <span className={styles.buildSummary}>
        Firmware build (<code>{latestRun.head_sha.slice(0, 5)}</code>) {mappedIcon}
        <br />
        <FuzzyTimestamp timestamp={new Date(latestRun.updated_at)} />
      </span>
      <Icon
        name="file-arrow-down"
        className={[
          styles.buildStatus,
          styles.buildSuccess
        ].join(' ')}
      />
    </Link>
  )
}

BuildLink.propTypes = {
  latestRun: PropTypes.shape({
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
    head_sha: PropTypes.string.isRequired,
    status: PropTypes.oneOf([
      'completed',
      'action_required',
      'cancelled',
      'failure',
      'neutral',
      'skipped',
      'stale',
      'success',
      'timed_out',
      'in_progress',
      'queued',
      'requested',
      'waiting'
    ]).isRequired,
    conclusion: PropTypes.oneOf([
      null,
      'action_required',
      'cancelled',
      'failure',
      'neutral',
      'success',
      'skipped',
      'stale',
      'timed_out'
    ]),
    created_at: PropTypes.string.isRequired,
    updated_at: PropTypes.string.isRequired
  })
}

export default BuildLink
