import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { GeneralHelpOfferCopy } from './support'

function KeymapBindingsUsePreprocessorMacros (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Could not parse keymap</h2>
        <p>
          It looks like some bindings in your keymap are using custom pre-processor
          macros which isn't supported by the Keymap Editor. These macros make
          it difficult both to resolve bindings to behaviors and parameters, as
          well as to generate modified keymap code that is consistent with your
          original intentions.
        </p>
        <p>
          Please either continue editing your keymap by hand with these helpers
          or if you would like to use this editor, rewrite the keymap to use
          ZMK's syntax directly.
        </p>
        {GeneralHelpOfferCopy}
      </DialogBox>
    </Modal>
  )
}

KeymapBindingsUsePreprocessorMacros.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default KeymapBindingsUsePreprocessorMacros
