import { createSlice } from '@reduxjs/toolkit'

export const selectCombos = state => state.keyboard.keymap.combos

const combosSlice = createSlice({
  name: 'combos',
  initialState: [],
  reducers: {
    updateComboBinding (state, action) {
      state[action.payload.comboIndex].bindings = action.payload.bindings
    },
    updateComboKeypositions (state, action) {
      state[action.payload.comboIndex].keyPositions = action.payload.keyPositions
    },
    createCombo (state) {
      state.push({
        identifier: 'new_combo',
        keyPositions: [],
        bindings: [{
          value: '&trans',
          params: []
        }]
      })
    },
    renameCombo (state, action) {
      state[action.payload.index].identifier = action.payload.name
    },
    deleteCombo (state, action) {
      state.splice(action.payload.index, 1)
    },
    customizeCombo (state, action) {
      Object.assign(state[action.payload.index], action.payload.config)
    }
  }
})

export const {
  updateComboBinding,
  updateComboKeypositions,
  createCombo,
  renameCombo,
  deleteCombo,
  customizeCombo
} = combosSlice.actions

export default combosSlice.reducer
