import { createContext, useMemo } from 'react'
import { getWrapperDimensions, getWrapperStyle } from '../Keyboard/util'

export const LayoutContext = createContext({
  layout: [],
  bindingCount: 0,
  wrapperDimensions: {},
  wrapperStyle: {}
})

export function LayoutContextProvider ({ layout, children }) {
  const bindingCount = useMemo(() => layout.length, [layout.length])
  const wrapperDimensions = useMemo(() => getWrapperDimensions(layout), [layout])
  const wrapperStyle = useMemo(() => getWrapperStyle(wrapperDimensions), [wrapperDimensions])

  const context = {
    layout,
    bindingCount,
    wrapperDimensions,
    wrapperStyle
  }

  return (
    <LayoutContext.Provider value={context}>
      {children}
    </LayoutContext.Provider>
  )
}
