import axios from 'axios'
import EventEmitter from 'eventemitter3'

import * as config from '../../config'

const websocketUrl = `${config.apiBaseUrl.replace('http', 'ws')}/github/events`

export class API extends EventEmitter {
  token = null
  initializing = false
  installations = null
  repositories = null
  repoInstallationMap = null
  features = null
  socket = null

  async _request (options) {
    if (typeof options === 'string') {
      options = {
        url: options
      }
    }

    if (options.url.startsWith('/')) {
      options.url = `${config.apiBaseUrl}${options.url}`
    }

    options.headers = Object.assign({}, options.headers)
    if (this.token && !options.headers.Authorization) {
      options.headers.Authorization = `Bearer ${this.token}`
    }

    try {
      return await axios(options)
    } catch (err) {
      if (err.response?.status === 401) {
        console.error('Authentication failed.')
        this.emit('authentication-failed', err.response)
      }

      throw err
    }
  }

  init () {
    if (this.initializing) {
      return this.initializing
    }

    this.initializing = (async () => {
      this.initEvents()

      const installationUrl = `${config.apiBaseUrl}/github/installation`
      const params = new URLSearchParams(window.location.search)

      // I don't really understand the callback parameters but as long as
      // *something* installation related has changed then its worth refreshing
      // the authentication.
      if (params.get('setup_action') && params.get('installation_id')) {
        this.beginLoginFlow()
      }

      if (params.get('blocked')) {
        setTimeout(() => this.emit('blocked'))
        return
      }

      const param = params.get('token')
      if (!window.localStorage.auth_token && param) {
        window.history.replaceState({}, null, window.location.pathname)
        window.localStorage.auth_token = param
      }

      if (window.localStorage.auth_token) {
        this.token = window.localStorage.auth_token
        const { data } = await this._request(installationUrl)
        const suspended = data.installations.filter(installation => installation.suspended_at)

        if (data.installations.length === suspended.length) {
          console.warn('No GitHub app installation found for authenticated user.')
          this.emit('app-not-installed')
        }

        this.username = data.username
        this.installations = data.installations
        this.repositories = data.repositories
        this.repoInstallationMap = data.repoInstallationMap
        this.emit('authenticated')
      }
    })()

    return this.initializing
  }

  initEvents () {
    const bufferedMessages = []
    const isOpen = () => {
      return this.socket.readyState === window.WebSocket.OPEN
    }

    const send = (message) => {
      if (isOpen()) {
        this.socket.send(message)
        return
      }

      bufferedMessages.push(message)
      console.log(
        'buffering websocket message',
        { readyState: this.socket.readyState },
        message
      )
    }

    const flush = () => {
      let message
      while ((message = bufferedMessages.shift())) {
        console.log('sending buffered websocket message', message)
        this.socket.send(message)
      }
    }

    this.socket = new window.WebSocket(websocketUrl)
    this.socket.onopen = () => {
      console.log(new Date(), 'connected to console')
      flush()
      setInterval(() => {
        send(JSON.stringify({ message: 'ping' }))
      }, 20000)
    }

    this.socket.onclose = () => console.log(new Date(), 'disconnected from server')
    this.socket.onerror = err => console.error(new Date(), err)
    this.socket.onmessage = event => {
      const message = JSON.parse(event.data)
      if (message.event === 'workflow_run') {
        this.emit('build-update', message.workflow_run)
      }
    }

    this.on('authenticated', () => {
      if (!this.installations?.length) {
        return
      }

      this.fetchAvailableFeatures()

      send(JSON.stringify({
        message: 'authorize',
        data: this.token
      }))
    })
  }

  beginLoginFlow () {
    window.localStorage.removeItem('auth_token')
    window.location.href = `${config.apiBaseUrl}/github/authorize`
  }

  beginInstallAppFlow () {
    window.location.href = `https://github.com/apps/${config.githubAppName}/installations/new`
  }

  manageInstallationRepos (installationId) {
    const installation = this.installations.find(installation => installation.id === installationId)
    window.location.href = installation.html_url
  }

  isGitHubAuthorized () {
    return !!this.token
  }

  isAppSuspendedForRepoSelection () {
    return (
      this.installations?.length > 0 &&
      this.installations?.every(installation => installation.suspended_at) &&
      this.installations?.some(installation => installation.repository_selection === 'all')
    )
  }

  isAppInstalled () {
    return this.installations?.length && this.repositories?.length
  }

  async fetchRepoBranches (repo) {
    const installation = encodeURIComponent(this.repoInstallationMap[repo.full_name])
    const repository = encodeURIComponent(repo.full_name)
    const { data } = await this._request(
      `/github/installation/${installation}/${repository}/branches`
    )

    return data
  }

  async fetchBranchKeyboards (repo, branch) {
    const installation = encodeURIComponent(this.repoInstallationMap[repo.full_name])
    const repository = encodeURIComponent(repo.full_name)
    const { data } = await this._request(
      `/github/installation/${installation}/${repository}/keyboards/${branch}`
    )

    return data
  }

  async fetchLayoutAndKeymap (repo, branch, { keyboard } = {}) {
    const installation = encodeURIComponent(this.repoInstallationMap[repo])
    const repository = encodeURIComponent(repo)
    const url = new URL(`${config.apiBaseUrl}/github/keyboard-files/${installation}/${repository}`)
    const query = new URLSearchParams()

    if (branch) {
      query.set('branch', branch)
    }
    if (keyboard) {
      query.set('keyboard', keyboard)
    }

    url.search = query.toString()

    try {
      const { data } = await this._request(url.toString())
      const { info: { layouts }, keymap } = data
      const defaultLayout = Object.keys(data.info.layouts)[0]
      const preferredLayout = keymap.chosen['zmk,matrix_transform']
        ? keymap.chosen['zmk,matrix_transform'].replace(/^&/, '')
        : null

      const layoutMetadata = (
        layouts[preferredLayout] ||
        layouts[defaultLayout]
      )
      const layout = layoutMetadata.layout

      return {
        layoutMetadata,
        layout,
        keymap,
        keyboards: data.keyboards,
        default: data.defaultKeymap,
        sensors: data.sensors
      }
    } catch (err) {
      if (err.response?.status === 400) {
        console.error('Failed to load keymap and layout from github', err.response.data)
        this.emit('repo-validation-error', err.response.data)
      } else if (err.response?.status === 500 && err.response.data.name === 'LayoutGenerationError') {
        this.emit('layout-generation-failed', err.response.data)
      }

      throw err
    }
  }

  async fetchAvailableFeatures () {
    if (!this.isGitHubAuthorized()) {
      return { betaFeatures: [] }
    }

    if (!this.features) {
      this.features = this._request(`${config.apiBaseUrl}/github/features`)
    }

    const { data } = await this.features
    return data
  }

  async fetchBuildStatus (repo, branch) {
    const installation = encodeURIComponent(this.repoInstallationMap[repo])
    const repository = encodeURIComponent(repo)
    const url = new URL(`${config.apiBaseUrl}/github/latest-run/${installation}/${repository}/${encodeURIComponent(branch)}`)

    const { data } = await this._request(url.toString())
    return data
  }

  commitChanges (repo, branch, layout, keymap, options = {}) {
    const {
      message = null,
      experimentalSettings = {},
      keyboard
    } = options
    const installation = encodeURIComponent(this.repoInstallationMap[repo])
    const repository = encodeURIComponent(repo)
    const url = new URL(`${config.apiBaseUrl}/github/keyboard-files/${installation}/${repository}/${encodeURIComponent(branch)}`)
    const { enableBehaviorEditing = false } = experimentalSettings
    const query = new URLSearchParams({
      enableBehaviorEditing
    })

    if (keyboard) {
      query.set('keyboard', keyboard)
    }

    url.search = query.toString()

    return this._request({
      url: url.toString(),
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      data: { keyboard, layout, keymap, message, enableBehaviorEditing }
    })
  }
}

const api = new API()
export default api
