import PropTypes from 'prop-types'

import KeyPositionSelector from '../../Common/KeyPositionSelector'
import {
  RawPropertyValue,
  ConstPropertyValue,
  StringPropertyValue,
  IntegerPropertyValue,
  BooleanPropertyValue
} from './simple-types'
import {
  EnumPropertyValue,
  MultiSelectPropertyValue
} from './collection-types'
import BehaviorsPropertyValue from './BehaviorsPropertyValue'
import BindingsPropertyValue from './BindingsPropertyValue'

function PropertyValue (props) {
  const { schema = {} } = props
  const { type } = schema
  if ('const' in schema) {
    return <ConstPropertyValue {...props} />
  }

  if ('enum' in schema) {
    return <EnumPropertyValue {...props} />
  }

  if (schema.helper === 'key-position-picker') {
    const { prop = {}, onUpdate } = props
    return (
      <KeyPositionSelector
        value={prop.parsed || []}
        onUpdate={value => {
          onUpdate({
            ...prop,
            useRaw: false,
            parsed: value
          })
        }}
      />
    )
  }

  if (schema.choices) {
    return <MultiSelectPropertyValue {...props} />
  }

  switch (type) {
    case 'string':
      return <StringPropertyValue {...props} />
    case 'int':
      return <IntegerPropertyValue {...props} />
    case 'boolean':
      return <BooleanPropertyValue {...props} />
    case 'phandle':
    case 'phandles':
      return <BehaviorsPropertyValue {...props} />
    case 'phandle-array':
      return <BindingsPropertyValue {...props} />
    default:
      return <RawPropertyValue {...props} />
  }
}

PropertyValue.propTypes = {
  schema: PropTypes.shape({
    type: PropTypes.oneOf([
      'string',
      'boolean',
      'int',
      'array',
      'phandle',
      'phandles',
      'phandle-array',

      'integer-union'
    ]),
    required: PropTypes.bool,
    description: PropTypes.string,
    default: PropTypes.any,
    const: PropTypes.any,
    helper: PropTypes.any
  }),
  prop: PropTypes.shape({
    parsed: PropTypes.any,
    raw: PropTypes.string
  }),
  onUpdate: PropTypes.func.isRequired
}

export default PropertyValue
