import { combineReducers, configureStore } from '@reduxjs/toolkit'

import appReducer, { INITIAL_STATE } from './appReducer'
import layersReducer from './Keyboard/Layers/layersSlice'
import combosReducer from './Keyboard/Combos/combosSlice'
import macrosReducer from './Keyboard/Macros/macrosSlice'
import behaviorsReducer from './Keyboard/Behaviors/behaviorsSlice'

const keyboardReducer = combineReducers({
  layout: (state = []) => state,
  sensors: (state = []) => state,
  keymap: combineReducers({
    warningFlags: (state = []) => state,
    defines: (state = []) => state,
    chosen: (state = {}) => state,
    behaviors: behaviorsReducer,
    combos: combosReducer,
    layers: layersReducer,
    macros: macrosReducer
  })
})

function rootReducer (state, action) {
  const newState = appReducer(state, action)
  return {
    ...newState,
    keyboard: newState.keyboard && {
      ...newState.keyboard,
      ...keyboardReducer(newState.keyboard, action)
    }
  }
}

const store = configureStore({
  reducer: rootReducer,
  preloadedState: INITIAL_STATE
})

export default store
