import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { GitHubIssuesLink } from './support'

function KeymapSaveFailed (props) {
  const { onDismiss } = props
  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Failed to save keymap changes</h2>
        <p>
          Sorry, an unexpected error occurred while trying to save your keymap.
        </p>
        <p>
          Please try again or raise an issue if the problem persists and I'll
          try to work it out with you. Seriously I can only get this right if I
          know what's wrong, and in some cases that comes down to people using
          this editor in more advanced ways than I myself have.
        </p>
        <p>
          {GitHubIssuesLink}
        </p>
      </DialogBox>
    </Modal>
  )
}

KeymapSaveFailed.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default KeymapSaveFailed
