import times from 'lodash/times'
import { behaviorSchemas } from 'zmk-data/behaviors'

function isBindingType (propSchema) {
  return ['phandle', 'phandles', 'phandle-array'].includes(propSchema.type)
}

export function createBehavior (compatible, { name, labels, label }) {
  const schema = behaviorSchemas[compatible]
  return {
    name,
    labels,
    properties: {
      compatible: {
        raw: `"${compatible}"`,
        parsed: compatible
      },
      label: {
        raw: `"${label}"`,
        parsed: label
      },
      ...Object.keys(schema.properties).reduce((props, prop) => {
        const propSchema = schema.properties[prop]
        if (isBindingType(propSchema) && propSchema.length) {
          props[prop] = {
            parsed: times(propSchema.length, () => '&none')
          }
        } else if ('const' in propSchema) {
          props[prop] = {
            parsed: propSchema.const,
            // this isn't right but conveniently only affects #binding-cells
            // which is an integer and which we never need to use as a raw value
            raw: `<${propSchema.const.toString()}>`
          }
        }

        return props
      }, {})
    }
  }
}
