import map from 'lodash/map'
import { useCallback, useContext, useMemo, useState } from 'react'
import ActionButton from '../../../Common/ActionButton'
import Icon from '../../../Common/Icon'
import Modal from '../../../Common/Modal'

import { DefinitionsContext } from '../../../Contexts/Definitions'
import ChoicesPicker from '../../../ValuePicker/ChoicesPicker'

import styles from './styles.module.css'

function useSchemaChoices (schema) {
  const definitions = useContext(DefinitionsContext)
  return useMemo(() => {
    if (!schema.choices) {
      return []
    }

    if (Array.isArray(schema.choices)) {
      return schema.choices
    }

    if (!schema.choices.$source) {
      throw new Error('Expected schema.choices.$source')
    }

    const source = definitions[schema.choices.$source]
    if (schema.choices.$key) {
      return map(source, schema.choices.$key)
    }

    return source
  }, [definitions, schema.choices])
}

export function EnumPropertyValue ({ prop = {}, schema, onUpdate }) {
  const choices = schema.enum
  const value = prop.parsed
  const handleChange = useCallback(event => {
    onUpdate({
      ...prop,
      parsed: choices[event.target.value],
      useRaw: false
    })
  }, [prop, choices, onUpdate])

  return (
    <select className={styles.propertyValue} onChange={handleChange} value={choices.indexOf(value)}>
      {choices.map((name, i) => (
        <option key={i} value={i}>{name}</option>
      ))}
    </select>
  )
}

export function MultiSelectPropertyValue ({ prop = {}, schema, onUpdate }) {
  const choices = useSchemaChoices(schema)
  const { parsed: selected = [] } = prop
  const handleChange = useCallback(selected => {
    onUpdate({ ...prop, useRaw: false, parsed: selected })
  }, [prop, onUpdate])

  const MultiSelect = choices.length > 20
    ? MultiSelectChips
    : MultiSelectCheckboxes

  return (
    <MultiSelect
      selected={selected}
      choices={choices}
      onUpdate={handleChange}
    />
  )
}

function MultiSelectCheckboxes ({ selected, choices, onUpdate }) {
  const handleChange = useCallback(choice => {
    onUpdate(
      selected.includes(choice)
        ? selected.filter(v => v !== choice)
        : [...selected, choice]
    )
  }, [selected, onUpdate])

  return (
    <ol className={styles.propertyMultiSelect}>
      {choices.map((choice, i) => (
        <li key={i}>
          <label>
            <input
              type="checkbox"
              checked={selected.includes(choice)}
              onChange={e => handleChange(choice)}
            />
            {choice}
          </label>
        </li>
      ))}
    </ol>
  )
}

function MultiSelectChips ({ selected, choices, onUpdate }) {
  const [adding, setAdding] = useState(false)
  const addChoice = useCallback(choice => {
    onUpdate([...selected, choice])
    setAdding(false)
  }, [setAdding, selected, onUpdate])
  const remove = useCallback(choice => {
    onUpdate(selected.filter(value => value !== choice))
  }, [selected, onUpdate])

  // TODO: make these CSS classes more generic if I want to share them between
  // unrelated components. (or refactor the elements themselves)
  return (
    <>
      <ul className={styles.propertyMultiSelectChips}>
        {selected.map((choice, i) => (
          <li key={i}>
            <span className={styles.behaviorBind}>
              {choice} {(
                <Icon
                  name="times"
                  className={styles.phandleRemove}
                  onClick={() => remove(choice)}
                />
              )}
            </span>
          </li>
        ))}
      </ul>
      <ActionButton secondary onClick={() => setAdding(true)}>
        <Icon name="plus" /> Add
      </ActionButton>
      {adding && (
        <Modal onDismiss={() => setAdding(false)}>
          <ChoicesPicker
            choices={choices}
            onSelect={addChoice}
          />
        </Modal>
      )}
    </>
  )
}
