import styles from './dialog-box.module.css'

export default function DialogBox (props) {
  const { dismissText = 'Ok', classes = [], onDismiss, children, ...rest } = props

  return (
    <div className={[styles.dialog, ...classes].join(' ')} {...rest}>
      {children}
      {dismissText && (
        <button className={styles.button} onClick={onDismiss}>
          {dismissText}
        </button>
      )}
    </div>
  )
}
