import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { KeymapLayoutResources } from './support'

function SuggestAutoLayoutTweak (props) {
  return (
    <Modal>
      <DialogBox {...props}>
        <h2>This layout was Auto-Generated</h2>
        <p>
          Hi there! There wasn't a layout definition to accompany your keymap
          file, so the keymap editor tried to generate one automatically in a
          last-ditch effort to help you get into the app.
        </p>
        <p>
          This layout definition was (or will be) saved when you first save
          keymap changes with the keymap editor, so remember that if there are
          any tweaks you'd like to make to improve it you can do so by editing
          the <code>info.json</code> file.
        </p>
        {KeymapLayoutResources}
      </DialogBox>
    </Modal>
  )
}

SuggestAutoLayoutTweak.propTypes = {}

export default SuggestAutoLayoutTweak
