import PropTypes from 'prop-types'
import { useState } from 'react'
import ActionButton from '../../Common/ActionButton'

import DialogBox from '../../Common/DialogBox'
import Modal from '../../Common/Modal'

const knownBehaviorTypes = [
  'zmk,behavior-hold-tap',
  'zmk,behavior-mod-morph',
  'zmk,behavior-tap-dance',
  'zmk,behavior-sticky-key',
  'zmk,behavior-caps-word'
]

function NewBehaviorModal ({ onUpdate, onCancel }) {
  const [pendingSelection, setPendingSelection] = useState('')
  return (
    <Modal>
      <DialogBox dismissText={null}>
        <h3>Add new behavior</h3>
        <label>
          Type <select
            required
            value={pendingSelection}
            onChange={e => setPendingSelection(e.target.value)}
          >
            <option value="" disabled>Select a behavior type</option>
            {knownBehaviorTypes.map((type, i) => (
              <option key={i} value={type}>{type}</option>
            ))}
          </select>
        </label>
        <div style={{ textAlign: 'center', margin: '10px' }}>
          <ActionButton
            disabled={!pendingSelection}
            onClick={() => onUpdate(pendingSelection)}
          >
            Create
          </ActionButton>
          <ActionButton onClick={onCancel} secondary>Cancel</ActionButton>
        </div>
      </DialogBox>
    </Modal>
  )
}

NewBehaviorModal.propTypes = {
  onUpdate: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired
}

export default NewBehaviorModal
