import Icon from '../../../Common/Icon'
import styles from './styles.module.css'

export function RawPropertyValue ({ prop, schema, onUpdate }) {
  return (
    <>
      <Icon name="code" title="Raw property value" /> <input
        required={schema?.required}
        value={prop?.raw}
        onChange={e => onUpdate({
          ...prop,
          raw: e.target.value,
          useRaw: true
        })}
      />
    </>
  )
}

export function ConstPropertyValue ({ schema }) {
  return (
    <span className={[
      styles.propertyValue,
      styles.readonly
    ].join(' ')}>
      {schema.const}
    </span>
  )
}

export function StringPropertyValue ({ prop, schema, onUpdate }) {
  return (
    <input
      type="text"
      className={styles.propertyValue}
      value={prop?.parsed ?? ''}
      placeholder={schema.default && `Default: ${schema.default}`}
      onChange={e => {
        onUpdate({
          ...prop,
          parsed: e.target.value,
          useRaw: false
        })
      }}
    />
  )
}

export function IntegerPropertyValue ({ prop, schema, onUpdate }) {
  return (
    <input
      type="number"
      className={styles.propertyValue}
      value={prop?.parsed ?? ''}
      placeholder={schema.default && `Default: ${schema.default}`}
      onChange={e => {
        onUpdate({
          ...prop,
          parsed: Number(e.target.value),
          useRaw: false
        })
      }}
    />
  )
}

export function BooleanPropertyValue ({ prop, onUpdate }) {
  return (
    <input
      type="checkbox"
      className={styles.propertyValue}
      checked={prop?.parsed || false}
      onChange={e => {
        onUpdate({
          parsed: !!e.target.checked,
          useRaw: false
        })
      }}
    />
  )
}
