import { useCallback, useState } from 'react'

import Icon from '../../../Common/Icon'
import ValuePicker from '../../../ValuePicker'
import { replaceIndex, withoutIndex } from '../../../reducer-util'

import styles from './styles.module.css'
import ActionButton from '../../../Common/ActionButton'
import { compact } from 'lodash'

export default function BehaviorssPropertyValue ({ prop = {}, schema = {}, onUpdate }) {
  const { parsed: selection = [] } = prop
  const { length } = schema
  const [editing, setEditing] = useState(null)

  const insertPhandle = useCallback(() => {
    onUpdate({
      ...prop,
      useRaw: false,
      parsed: [...selection, '']
    })

    setEditing(selection.length)
  }, [prop, selection, onUpdate, setEditing])

  const updatePhandle = useCallback(result => {
    onUpdate({
      ...prop,
      useRaw: false,
      parsed: replaceIndex(selection, editing, result.code)
    })

    setEditing(null)
  }, [prop, selection, editing, onUpdate])

  const removePhandle = useCallback(index => {
    onUpdate({
      ...prop,
      useRaw: false,
      parsed: withoutIndex(selection, index)
    })
  }, [prop, selection, onUpdate])

  return (
    <div>
      <ol className={compact([
        styles.propertyPhandleSelect,
        length === 1 && styles.propertyPhandleSingleton
      ]).join(' ')}>
        {selection.map((phandle, i) => (
          <li key={i}>
            <span
              className={styles.behaviorBind}
              onClick={() => setEditing(i)}
            >
              {phandle}
              {length !== 1 && (
                <Icon
                  name="times"
                  className={styles.phandleRemove}
                  onClick={event => {
                    event.stopPropagation()
                    removePhandle(i)
                  }}
                />
              )}
            </span>
          </li>
        ))}
      </ol>
      {editing !== null && (
        <ValuePicker
          node={{ value: selection[editing], params: [], slot: 'behaviour' }}
          type="value"
          context="none"
          behaviour={{ code: selection[editing] }}
          prompt="Select Behavior"
          searchKey="code"
          onSelect={updatePhandle}
          onCancel={() => setEditing(null)}
        />
      )}
      {length && selection.length < length && (
        <ActionButton secondary onClick={insertPhandle}>
          <Icon name="plus" /> Add Behavior
        </ActionButton>
      )}
    </div>
  )
}
