function Tabs ({ text, tabs, defaultTab, children }) {
  const [activeTab, setActiveTab] = useState(defaultTab)

  return (
    <>
      <h3 style={{ display: 'inline' }}>{text}</h3>
      <ul className={styles.editorModeTabs}>
        {tabs.map(({ name: tab, tabClasses = [] }, i) => (
          <li
            key={i}
            onClick={() => setActiveTab(i)}
            className={compact([
              activeTab === i && styles.activeEditTab,
              ...tabClasses
            ]).join(' ')}
          >
            {tab}
          </li>
        ))}
      </ul>
      {children[activeTab]}
    </>
  )
}

const selectKeymap = state => state.keyboard.keymap

function Keyboard (props) {
  const keymap = useSelector(selectKeymap)
  const { layout } = useContext(LayoutContext)
  const { sensors } = props
  const sanityChecks = useMemo(() => sanityCheck(keymap, layout), [keymap, layout])
  const warningCount = useMemo(() => {
    const { nodeNames, nodeLabels, nodeReferences, deprecations, layoutMissingPositions } = sanityChecks
    return (
      nodeNames.duplicates.length +
      nodeNames.redefined.length +
      nodeNames.passwordMacros.length +
      nodeLabels.duplicates.length +
      nodeReferences.duplicates.length +
      deprecations.resetBindings.length +
      layoutMissingPositions.length
    )
  }, [sanityChecks])

  const tabs = compact([
    { name: 'layers', component: <LayerEditor sensors={sensors} /> },
    { name: 'combos', component: <ComboEditor /> },
    { name: 'macros', component: <MacroEditor /> },
    { name: <span className={styles.beta}>behaviors</span>, component: <Behaviors /> },
    warningCount > 0 && ({
      name: `warnings (${warningCount})`,
      tabClasses: [styles.warningTab],
      component: <Warnings sanityChecks={sanityChecks} />
    })
  ])

  const defaultTab = 0

  return (
    <Context keymap={keymap}>
      <Tabs text="Editor mode:" tabs={tabs} defaultTab={defaultTab}>
        {tabs.map((tab, i) => (
          <Fragment key={i}>
            {tab.component}
          </Fragment>
        ))}
      </Tabs>
    </Context>
  )
}

Keyboard.propTypes = {
  keymap: keymapProptypes.keymap
}

export default Keyboard
