import { compact } from 'lodash'

import * as api from './api'

export function loadKeycodes () {
  return api.loadKeycodes().then(normalizeZmkKeycodes)
}

export function loadKeycodeGlyphs () {
  return api.loadKeycodeGlyphs()
}

function shortestAlias (aliases) {
  return [...aliases]
    .sort((a, b) => a.length - b.length)[0]
}

const fnPattern = /^(.+?)\((code)\)$/

function normalizeZmkKeycodes (keycodes) {
  return keycodes.reduce((keycodes, keycode) => {
    const { description, context, symbol, faIcon } = keycode
    const aliases = keycode.names.filter(name => !name.match(fnPattern))
    const name = shortestAlias(aliases)
    const fnCode = keycode.names.map(name => name.match(fnPattern)).filter(v => !!v)[0]
    const base = {
      name,
      aliases,
      description,
      context,
      faIcon,
      symbol: symbol || name,
      params: []
    }

    for (const code of aliases) {
      keycodes.push(Object.assign({}, base, {
        code,
        isModifier: !!fnCode
      }))
    }

    if (fnCode) {
      keycodes.push(Object.assign({}, base, {
        code: fnCode[1],
        params: fnCode[2].split(',')
      }))
    }

    return keycodes
  }, [])
}

export function getMergedGlyphs (glyphs, options) {
  return compact([
    glyphs.basic,
    options.unicode && glyphs.unicode,
    options.platform === 'mac' && glyphs.platforms.mac,
    options.platform === 'win' && glyphs.platforms.win
  ]).reduce((glyphs, collection) => {
    for (const overlay of collection) {
      const aliases = overlay.names.filter(name => !name.match(fnPattern))
      const name = shortestAlias(aliases)

      if (!glyphs[name]) {
        glyphs[name] = {}
      }

      glyphs[name].symbol = glyphs[name].symbol || overlay.symbol
      glyphs[name].faIcon = glyphs[name].faIcon || overlay.faIcon
    }
    return glyphs
  }, {})
}
