import { useCallback, useEffect, useRef, useState } from 'react'
import PropTypes from 'prop-types'

import styles from './editablefield.module.css'
import Icon from './Icon'
import { stop, onKey } from '../event-util'

function EditableField ({ value, validate, required, render, onUpdate }) {
  const ref = useRef()
  const [state, setState] = useState({
    value,
    editing: false,
    attemptedEmpty: false
  })

  const error = state.attemptedEmpty
    ? 'cannot be empty'
    : (validate && validate(state.value))

  const focusInput = useCallback(node => {
    if (node) {
      node.focus()
      node.select()
    }
  }, [])

  function finishEditing () {
    if (!state.editing) {
      return
    }

    if (state.value === '' && required) {
      setState({ ...state, attemptedEmpty: true })
      return
    }

    if (!error) {
      onUpdate(state.value)
    }

    setState({ ...state, value: state.value, editing: false })
  }

  const cancelEditing = useCallback(() => {
    setState(state => ({ ...state, editing: false }))
  }, [setState])

  const handleClickOutside = useCallback(event => {
    const clickedOutside = ref.current && !ref.current.contains(event.target)
    if (!clickedOutside) {
      return
    }

    cancelEditing()
  }, [ref, cancelEditing])

  useEffect(() => {
    document.addEventListener('click', handleClickOutside)
    return () => document.removeEventListener('click', handleClickOutside)
  }, [handleClickOutside])

  if (state.editing) {
    return (
      <span className={styles.container} ref={ref}>
        <input
          ref={focusInput}
          value={state.value}
          onChange={e => setState({
            ...state,
            attemptedEmpty: false,
            value: e.target.value
          })}
          onKeyDown={onKey({
            Enter: finishEditing,
            Escape: cancelEditing
          })}
        />
        {error && <span className={styles.error}>{error}</span>}
      </span>
    )
  }

  return render(
    <>
      <span>{value}</span>
      <Icon
        name="pencil"
        className={styles.edit}
        onClick={stop(() => setState({ ...state, editing: true }))}
      />
    </>
  )
}

EditableField.propTypes = {
  value: PropTypes.string.isRequired,
  validate: PropTypes.func,
  required: PropTypes.bool.isRequired,
  render: PropTypes.func.isRequired,
  onUpdate: PropTypes.func.isRequired
}

EditableField.defaultProps = {
  required: true
}

export default EditableField
