import get from 'lodash/get'
import pick from 'lodash/pick'

function init () {
  const settings = JSON.parse(
    localStorage.getItem('experimental-settings') || '{}'
  )

  return {
    definitions: null,
    source: null,
    sourceOther: null,
    keyboard: null,
    _unmodifiedKeymap: null,
    saving: null,
    saveError: null,
    settingsVisible: null,
    commitDialogOpen: false,
    theme: JSON.parse(localStorage.getItem('theme' || '"system"')),
    experimentalSettings: {
      unicodeGlyphs: get(settings, 'unicodeGlyphs', true),
      platformGlyphs: get(settings, 'platformGlyphs', 'auto'),
      enableBehaviorEditing: get(settings, 'enableBehaviorEditing', false)
    },
    latestBuild: null
  }
}

export const INITIAL_STATE = init()

export default function reducer (state, action) {
  const { type } = action

  switch (type) {
    case 'INITIALIZE':
      return init()

    case 'DEFINITIONS_SET':
      return { ...state, definitions: action.definitions }

    case 'CLEAR_KEYBOARD_SELECTION':
      return { ...state, keyboard: null }

    case 'SELECT_KEYBOARD':
      return selectKeyboard(state, action)

    case 'COMMIT_DIALOG_OPEN':
      return { ...state, commitDialogOpen: true }

    case 'COMMIT_DIALOG_CLOSE':
      return { ...state, commitDialogOpen: false }

    case 'GITHUB_BUILD_UPDATE':
      return { ...state, latestBuild: action.build }

    case 'KEYMAP_SAVE_START':
      return { ...state, saving: true, saveError: false }

    case 'KEYMAP_SAVE_SUCCESS':
      return {
        ...state,
        saving: false,
        unsaved: false,
        keyboard: {
          ...state.keyboard,
          keymap: action.keymap || state.keyboard.keymap
        },
        _unmodifiedKeymap: action.keymap || state.keyboard.keymap
      }

    case 'KEYMAP_SAVE_FAILURE':
      return {
        ...state,
        saving: false,
        saveError: true
      }

    case 'CLEAR_SAVE_ERROR':
      return { ...state, saveError: null }

    case 'SETTINGS_OPEN':
      return { ...state, settingsVisible: true }

    case 'SETTINGS_CLOSE':
      return { ...state, settingsVisible: false }

    case 'SETTINGS_UPDATE':
      localStorage.setItem('experimental-settings', JSON.stringify(action.settings))
      return { ...state, experimentalSettings: action.settings }

    case 'CYCLE_THEME':
      return cycleTheme(state, action)

    default:
      return state
  }
}

// Here we can adapt the payload to account for API changes
function selectKeyboard (state, action) {
  const { keyboard } = state
  const newKeyboardState = pick(action, ['layout', 'keymap', 'sensors'])

  newKeyboardState.keymap.macros = newKeyboardState.keymap.macros.map(macro => (
    pick(macro, ['index', 'name', 'labels', 'properties'])
  ))

  return {
    ...state,
    source: action.source,
    sourceOther: action.sourceOther,
    keyboard: { ...keyboard, ...newKeyboardState },
    _unmodifiedKeymap: action.keymap
  }
}

function cycleTheme (state, action) {
  const themes = ['light', 'dark', 'system']
  const index = themes.indexOf(state.theme)
  const theme = themes.at((index + 1) % themes.length)
  localStorage.setItem('theme', `"${theme}"`)
  document.body.dataset.theme = theme
  return { ...state, theme }
}
