import cloneDeep from 'lodash/cloneDeep'
import filter from 'lodash/filter'
import keyBy from 'lodash/keyBy'
import { createContext, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

// TODO: probably import these directly from zmk-data?
import { loadBehaviourBindings } from '../api'
import { loadKeycodeGlyphs, loadKeycodes } from '../keycodes'

export const DefinitionsContext = createContext({
  keycodes: [],
  behaviours: [],
  behaviors: {},
  behaviorBinds: [],
  glyphs: []
})

// TODO: I bet this doesn't need to be in the app state at all
const selectDefinitions = state => state.definitions

export function DefinitionsContextProvider ({ children }) {
  const definitions = useSelector(selectDefinitions)
  const dispatch = useDispatch()

  useEffect(() => {
    (async function () {
      if (definitions !== null) {
        return
      }

      const [keycodes, behaviors, glyphs] = await Promise.all([
        loadKeycodes(),
        loadBehaviourBindings(),
        loadKeycodeGlyphs()
      ])

      // TODO: update references to better understand the difference between
      // "behaviors" (e.g. hold tap) and the "behavior bindings" that implement
      // them (e.g. &lt and &mt)
      const schemas = cloneDeep(behaviors.behaviors)
      const behaviours = cloneDeep(behaviors.bindings)

      keycodes.indexed = keyBy(keycodes, 'code')
      behaviours.indexed = keyBy(behaviours, 'code')
      behaviours.context = {
        none: behaviours.filter(({ context }) => !context),
        macros: filter(behaviours, { context: 'macros' })
      }
      behaviours.byContext = {
        none: keyBy(behaviours.context.none, 'code'),
        macros: keyBy(behaviours.context.macros, 'code')
      }

      dispatch({
        type: 'DEFINITIONS_SET',
        definitions: {
          keycodes,
          behaviours,
          behaviors: schemas,
          behaviorBinds: behaviours,
          glyphs
        }
      })
    })()
  }, [definitions, dispatch])

  // TODO: show a spinner MAYBE
  if (!definitions) {
    return
  }

  return (
    <DefinitionsContext.Provider value={definitions}>
      {children}
    </DefinitionsContext.Provider>
  )
}
