import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { GeneralHelpOfferCopy } from './support'

function KeymapParsingFailed (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Could not parse keymap</h2>
        <p>
          Something went wrong while trying to parse your keymap. It's hard to
          say what exactly, because I wasn't expecting this.
        </p>
        <p>
          A common occurrence here is keymaps using a variety of pre-processor
          macros to define layers/combos/etc.
        </p>
        {GeneralHelpOfferCopy}
      </DialogBox>
    </Modal>
  )
}

KeymapParsingFailed.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default KeymapParsingFailed
