import styles from './styles.module.css'

export default function PropertyName ({ property, schema }) {
  return (
    <>
      <span className={styles.propertyName} title={property}>
        {property}
      </span>
      {schema.deprecatedBy && (
        <sub className={styles.propertyDeprecation}>
          (deprecated)
        </sub>
      )}
    </>
  )
}
