
export function sanityCheckLayout (layout) {
  return {
    layoutMissingPositions: sanityCheckPosiitions(layout)
    // overlappedPositions: sanityCheckOverlappingPositions(layout),
    // unorderedRows: sanityCheckRowOrdering(layout),
    // undereredColumns: sanityCheckColumnOrdering(layout)
  }
}

function sanityCheckPosiitions (layout) {
  const noKeyHasPosition = layout.every(key => (
    key.row === undefined &&
    key.col === undefined
  ))

  return noKeyHasPosition
    ? ['Layout in info.json has no row/col definitions. Generated keymap files will not be nicely formatted.']
    : []
}

function sanityCheckOverlappingPositions (layout) {
  const errors = []
  const keysByPosition = layout.reduce((acc, key, i) => {
    const { row, col } = key
    const position = `(${row},${col})`

    if (row !== undefined && col !== undefined) {
      acc[position] = [...(acc[position] || []), i]
    }

    return acc
  }, {})

  for (const group of Object.values(keysByPosition)) {
    if (group.length <= 1) {
      continue
    }

    const { row, col } = layout[group[0]]
    for (const index of group) {
      errors.push({
        location: `Layout JSON [${index}]`,
        position: { row, col }
      })
    }
  }

  return errors
}

function sanityCheckRowOrdering (layout) {
  let currentRow = -Infinity
  const errors = []

  for (const index of layout.keys()) {
    const { row } = layout[index]
    if (row === undefined) {
      continue
    }

    if (row < currentRow) {
      errors.push({ location: `Layout JSON [${index}]` })
    } else {
      currentRow = row
    }
  }

  return errors
}

function sanityCheckColumnOrdering (layout) {
  let currentRow = 0
  let currentCol = -Infinity
  const errors = []

  for (const index of layout.keys()) {
    const { row, col } = layout[index]
    if (row === undefined || col === undefined) {
      continue
    }

    if (currentRow !== row) {
      currentRow = row
      currentCol = -Infinity
    }

    if (col < currentCol) {
      errors.push({ location: `Layout JSON [${index}]` })
    } else {
      currentCol = col
    }
  }

  return errors
}
