import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { GitHubIssuesLink } from './support'

function MissingRootNode (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Could not parse keymap</h2>
        <p>
          Missing devicetree root node.
        </p>
        <p>
          The keymap editor was unable to find a devicetree root node in your
          keymap. This means that either something is wrong with the parser or
          something is wrong with your keymap's syntax.
        </p>
        <p>
          If you've been using the keymap editor succesfully up to now it's
          possible that it has introduced an error in the keymap file. Please
          reach out for support at {GitHubIssuesLink}.
        </p>
      </DialogBox>
    </Modal>
  )
}

MissingRootNode.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default MissingRootNode
