import find from 'lodash/find'
import PropTypes from 'prop-types'
import { useCallback, useEffect, useRef, useState } from 'react'

import LocalPicker from './Local/Picker'
import DemoPicker from './Demo/Picker'
import GithubPicker from './Github/Picker'
import { sourceChoices, defaultSource } from './sources'
import { Button, Select } from './Controls'
import { getErrorDialog } from './errors'
import SuggestAutoLayoutTweak from '../Dialogs/SuggestAutoLayoutTweak'
import Icon from '../Common/Icon'

function useToggle (initial) {
  const [toggleState, setToggleState] = useState(initial)
  const setTrue = useCallback(() => setToggleState(true), [setToggleState])
  const setFalse = useCallback(() => setToggleState(false), [setToggleState])
  const setInvert = useCallback(() => setToggleState(state => !state), [setToggleState])

  return [toggleState, setTrue, setFalse, setInvert]
}

function useClickOutsideHandler (ref, callback, enabled = true) {
  useEffect(() => {
    if (enabled) {
      const handleClick = event => {
        const isInDocument = document.body.contains(event.target)
        const isInWrapper = ref.current.contains(event.target)
        if (isInDocument && !isInWrapper) {
          callback()
        }
      }

      document.body.addEventListener('click', handleClick)
      return () => document.body.removeEventListener('click', handleClick)
    }
  }, [ref, enabled, callback])
}

function KeyboardPicker (props) {
  const { onSelect } = props
  const wrapperRef = useRef()
  const [source, setSource] = useState(defaultSource)
  const [{ autoGenerated }, setLayoutInfo] = useState({ autoGenerated: false })
  const [showDialog, setShowDialog] = useState(null)
  const closeDialog = useCallback(() => setShowDialog(null), [setShowDialog])
  const [isExpanded, setExpanded, setCompact] = useToggle(true)
  const showLayoutHelp = useCallback(() => {
    setShowDialog({
      component: SuggestAutoLayoutTweak,
      onClose: closeDialog
    })
  }, [setShowDialog, closeDialog])

  useClickOutsideHandler(wrapperRef, setCompact, isExpanded && source === 'github')

  const handleKeyboardSelected = useCallback(function (event) {
    const { warningFlags = [] } = event.keymap
    const { autoGenerated = false } = event.layoutMetadata || {}
    const error = getErrorDialog(warningFlags)

    if (error) {
      setShowDialog({ component: error, onClose: closeDialog })
      onSelect({ source, keyboard: null })
      return
    }

    setLayoutInfo({ autoGenerated })
    onSelect({ source, ...event })
  }, [onSelect, source, setShowDialog, closeDialog])

  const handleError = useCallback(function ({ dialog, dialogProps }) {
    setShowDialog({ component: dialog, dialogProps, onClose: closeDialog })
    onSelect({ source, keyboard: null })
  }, [setShowDialog, closeDialog, onSelect, source])

  useEffect(() => {
    localStorage.setItem('selectedSource', source)
  }, [source])

  // TODO: Use source to memoize a Picker as defined in './sources'
  // TODO: Turn this into a context? Can provide:
  // - renderable picker,
  // - renderable actions,
  // - callbacks to load/reload

  const selectedSource = find(sourceChoices, { id: source })

  return (
    <div ref={wrapperRef} style={{
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'end',
      gap: '5px',
      margin: '5px'
    }}>
      <Select
        label="Source"
        value={selectedSource}
        options={sourceChoices}
        getOptionLabel={option => option.name}
        getOptionValue={option => option.id}
        onChange={({ id }) => setSource(id)}
      />

      {showDialog?.component && <showDialog.component {...showDialog.dialogProps || {}} onDismiss={showDialog.onClose} />}

      {source === 'local' && <LocalPicker onError={handleError} onSelect={handleKeyboardSelected} />}
      {source === 'demo' && <DemoPicker onSelect={handleKeyboardSelected} />}
      {source === 'github' && (
        <GithubPicker
          expanded={isExpanded}
          onSelect={handleKeyboardSelected}
          onError={handleError}
          onExpand={setExpanded}
        />
      )}

      {autoGenerated && (
        <Button onClick={showLayoutHelp}>
          <Icon name="warning" /> Layout wrong?
        </Button>
      )}
    </div>
  )
}

KeyboardPicker.propTypes = {
  onSelect: PropTypes.func.isRequired
}

export default KeyboardPicker
