import omit from 'lodash/omit'
import { createSlice } from '@reduxjs/toolkit'
import { resolveBehaviorParams } from 'zmk-data/parameters'

export const selectBehaviors = state => state.keyboard.keymap.behaviors

const behaviorsSlice = createSlice({
  name: 'behaviors',
  initialState: [],
  reducers: {
    addBehavior (state, action) {
      state.push(action.payload.behavior)
    },
    updateBehavior (state, action) {
      state[action.payload.behaviorIndex] = omit(action.payload.behavior, 'params')
      resolveBehaviorParams(state)
    },
    deleteBehavior (state, action) {
      state.splice(action.payload.behaviorIndex, 1)
    }
  }
})

export const {
  addBehavior,
  updateBehavior,
  deleteBehavior
} = behaviorsSlice.actions

export default behaviorsSlice.reducer
