import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'

function ZmkNodefreeConfig (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Could not parse keymap</h2>
        <p>
          Detected use of <code>zmk-nodefree-config</code>
        </p>
        <p>
          It looks like your keymap is built using helper functions from the
          zmk-nodefree-config library to replace devicetree syntax. Parsing and
          editing keymaps that use this syntax is unsupported by the Keymap
          Editor.
        </p>
        <p>
          Please either continue editing your keymap by hand with these helpers
          or if you would like to use this editor, rewrite the keymap to use
          ZMK's syntax directly.
        </p>
      </DialogBox>
    </Modal>
  )
}

ZmkNodefreeConfig.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default ZmkNodefreeConfig
