import { transformKeyPolygon, getKeyBoundingBox } from '../key-units'

export function getLayoutPolygons (layout, { scale = 1 }) {
  return layout.map(key => transformKeyPolygon(
    { x: key.x, y: key.y },
    { u: key.u || key.w || 1, h: key.h || 1 },
    { x: key.rx, y: key.ry, a: key.r }
  ))
}

export function getWrapperDimensions (layout) {
  return layout.map(key => getKeyBoundingBox(
    { x: key.x, y: key.y },
    { u: key.u || key.w || 1, h: key.h || 1 },
    { x: key.rx, y: key.ry, a: key.r }
  )).reduce(({ x, y }, { max }) => ({
    x: Math.max(x, max.x),
    y: Math.max(y, max.y)
  }), { x: 0, y: 0 })
}

export function getWrapperStyle (layoutOrBBox, { scale = 1, overrides = {} } = {}) {
  const bbox = Array.isArray(layoutOrBBox)
    ? getWrapperDimensions(layoutOrBBox)
    : layoutOrBBox

  return {
    width: `${bbox.x * scale}px`,
    height: `${bbox.y * scale}px`,
    margin: '0 auto',
    padding: '0 40px 40px',
    ...overrides
  }
}
