import { useCallback } from 'react'
import ActionButton from '../../../Common/ActionButton'
import Icon from '../../../Common/Icon'
import { replaceIndex, withoutIndex } from '../../../reducer-util'

import Key from '../../Keys/Key'
import KeyWrapper from '../../Keys/KeyWrapper'

import styles from './styles.module.css'

export default function BindingsPropertyValue ({ prop = {}, onUpdate }) {
  const { parsed: selection = [] } = prop

  const insertBinding = useCallback(() => {
    onUpdate({
      ...prop,
      useRaw: false,
      parsed: [...selection, { value: '', params: [] }]
    })
  }, [prop, selection, onUpdate])

  const updateBinding = useCallback((binding, i) => {
    onUpdate({
      ...prop,
      useRaw: false,
      parsed: replaceIndex(selection, i, binding)
    })
  }, [prop, selection, onUpdate])

  const removeBinding = useCallback(index => {
    onUpdate({
      ...prop,
      useRaw: false,
      parsed: withoutIndex(selection, index)
    })
  }, [prop, selection, onUpdate])

  return (
    <>
      <ol className={styles.propertyBindingsValue}>
        {selection.map((binding, i) => (
          <li key={i}>
            <KeyWrapper>
              <Key
                context="none"
                value={binding.value}
                params={binding.params}
                onUpdate={binding => updateBinding(binding, i)}
              />
              <div className={styles.actions}>
                <Icon
                  name="trash"
                  className={styles.deleteBind}
                  onClick={e => {
                    e.stopPropagation()
                    removeBinding(i)
                  }}
                />
              </div>
            </KeyWrapper>
          </li>
        ))}
      </ol>
      <ActionButton secondary onClick={insertBinding}>
        <Icon name="plus" /> Add binding
      </ActionButton>
    </>
  )
}
