import cloneDeep from 'lodash/cloneDeep'
import omit from 'lodash/omit'
import { useCallback, useContext, useState } from 'react'

import ActionButton from '../../Common/ActionButton'
import DialogBox from '../../Common/DialogBox'
import Icon from '../../Common/Icon'
import Modal from '../../Common/Modal'
import { DefinitionsContext } from '../../Contexts/Definitions'
import BehaviorHeader from './BehaviorHeader'
import PropertyName from './PropertyName'
import PropertyValue from './PropertyValue'
import styles from './styles.module.css'

function BehaviorEditor ({ node, onUpdate, onCancel }) {
  const { behaviors: schemas } = useContext(DefinitionsContext)
  const [pendingChanges, setPendingChanges] = useState(node)
  const schema = schemas[node.properties.compatible?.parsed]

  const handlePropNameChange = prop => event => {
    setPendingChanges({
      ...pendingChanges,
      properties: Object.keys(pendingChanges.properties).reduce((acc, key) => {
        const value = pendingChanges.properties[key]
        if (key === prop) {
          key = event.target.value
        }

        acc[key] = value
        return acc
      }, {})
    })
  }

  const handlePropValueChange = name => value => {
    setPendingChanges({
      ...pendingChanges,
      properties: {
        ...pendingChanges.properties,
        [name]: value
      }
    })
  }

  const handleDeleteProp = name => {
    setPendingChanges({
      ...pendingChanges,
      properties: omit(pendingChanges.properties, name)
    })
  }

  const confirm = useCallback(() => {
    onUpdate(cloneDeep(pendingChanges))
  }, [pendingChanges, onUpdate])

  const schemaProps = schema?.properties || {}
  const propertiesList = (
    schema
      ? Object.keys(schemaProps)
      : Object.keys(pendingChanges.properties)
  ).filter(property => {
    return property !== 'label' && (
      !schemaProps[property]?.deprecatedBy ||
      !!pendingChanges.properties[property]
    )
  })

  return (
    <Modal onDismiss={onCancel}>
      <DialogBox dismissText={false}>
        <div style={{ maxHeight: '80vh', overflow: 'auto' }}>
          <h2>Edit {node.name} <code>(&amp;{node.labels[0]})</code></h2>
          <BehaviorHeader
            node={pendingChanges}
            onUpdate={setPendingChanges}
          />

          <h3>Configuration</h3>
          <table className={styles.editorForm}>
            <thead>
              <tr>
                <th>Property</th>
                <th>Value</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {propertiesList.map((name, i) => (
                <tr key={i}>
                  <td className={styles.propertyName}>
                    {name in schemaProps
                      ? (
                      <PropertyName property={name} schema={schemaProps[name]} />
                        )
                      : (
                      <input value={name} onChange={handlePropNameChange(name)} />
                        )}
                  </td>
                  <td className={styles.propertyValue}>
                    <PropertyValue
                      name={name}
                      prop={pendingChanges.properties[name]}
                      schema={schemaProps[name]}
                      onUpdate={handlePropValueChange(name)}
                    />
                  </td>
                  <td>
                    {(pendingChanges.properties[name] && !schemaProps[name]?.required) && (
                      <Icon
                        name="trash-can"
                        className={styles.action}
                        onClick={() => handleDeleteProp(name)}
                      />
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          <div style={{ textAlign: 'center' }}>
            <ActionButton onClick={confirm}>Okay</ActionButton>
            <ActionButton onClick={onCancel} secondary>Cancel</ActionButton>
          </div>
        </div>
      </DialogBox>
    </Modal>
  )
}

export default BehaviorEditor
