import pick from 'lodash/pick'
import { useState } from 'react'
import { useSelector } from 'react-redux'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import styles from './styles.module.css'
import { selectLayers } from '../Layers/layersSlice'

function MultiSelect ({ selected, options, onUpdate }) {
  function handleChange (index) {
    onUpdate(
      selected.includes(index)
        ? selected.filter(i => i !== index)
        : [...selected, index]
    )
  }
  return (
    <ul className={styles.layerList}>
      {options.map((option, i) => (
        <li
          data-selected={selected.includes(i)}
          onClick={() => handleChange(i)}
        >
          {option}
        </li>
      ))}
    </ul>
  )
}

function ConfigModal ({ combo, onUpdate }) {
  const layers = useSelector(selectLayers)
  const [pending, setPending] = useState(pick(combo, [
    'timeoutMs',
    'slowRelease',
    'layers'
  ]))

  const availableLayers = layers.map(layer => layer.name)
  function handleConfirm () {
    onUpdate(pending)
  }

  return (
    <Modal>
      <DialogBox onDismiss={handleConfirm}>
        <h2>Customize [<code>{combo.identifier}</code>]</h2>
        <div>

          <div className={styles.field}>
            <label htmlFor="customize-combo-timeout">
              Timeout (ms)
            </label>
            <input
              id="customize-combo-timeout"
              type="number"
              placeholder="Default"
              value={pending.timeoutMs}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  timeoutMs: event.target.value === ''
                    ? undefined
                    : Number(event.target.value)
                }))
              )}
            />
          </div>

          <div className={styles.field}>
            <label htmlFor="customize-combo-slow-release">
              Slow Release
            </label>
            <input
              id="customize-combo-slow-release"
              type="checkbox"
              checked={pending.slowRelease}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  slowRelease: event.target.checked
                }))
              )}
            />
          </div>

          <div className={styles.field}>
            <label>Layers</label>
            <MultiSelect
              selected={pending.layers || []}
              options={availableLayers}
              onUpdate={layers => (
                setPending(state => ({
                  ...state,
                  layers
                }))
              )}
            />
          </div>
        </div>
      </DialogBox>
    </Modal>
  )
}

export default ConfigModal
