import KeymapBindingsUsePreprocessorMacros from '../Dialogs/KeymapBindingsUsePreprocessorMacros'
import KeymapParsingFailed from '../Dialogs/KeymapParsingFailed'
import KeymapUsingMiryoku from '../Dialogs/KeymapUsingMiryoku'
import MissingRootNode from '../Dialogs/MissingRootNode'
import ZmkNodefreeConfig from '../Dialogs/ZmkNodefreeConfig'

export function getErrorDialog (warningFlags) {
  switch (warningFlags[0]) {
    case 'USING_ZMK_NODEFREE_MACROS':
      return ZmkNodefreeConfig
    case 'USING_MIRYOKU_DTSI':
      return KeymapUsingMiryoku
    case 'USING_NON_ZMK_MACRO':
      return KeymapBindingsUsePreprocessorMacros
    case 'KEYMAP_PARSING_FAILED':
      return KeymapParsingFailed
    case 'MISSING_ROOT_NODE':
      return MissingRootNode
    default:
      return null
  }
}
