import DialogBox from '../../Common/DialogBox'
import Link from '../../Common/Link'
import Modal from '../../Common/Modal'

export default function BetaNoticeDialog ({ onDismiss }) {
  const issuesLink = (
    <Link external href="https://github.com/nickcoutsos/keymap-editor/issues">
      Keymap Editor Issues
    </Link>
  )

  return (
    <Modal onDismiss={onDismiss}>
      <DialogBox onDismiss={onDismiss} style={{ maxHeight: '80vh', overflow: 'scroll' }}>
        <h2>Behavior Editing Beta Test</h2>
        <p>Welcome to the behavior editing beta!</p>

        <p>
          This feature has been a long time coming, and while it should enable a
          lot of advanced ZMK features through the app it is still a work in
          progress. Some features are still missing so the use experience is a
          little rough, and there may be errors as well. Some familiarity with
          ZMK behavior functionality is expected.
        </p>

        <p>
          I've been using it for a while and it seems to be in a pretty workable
          state, but I can't catch everything, so it would mean a lot to me for
          you to share any feedback or issues on GitHub: {issuesLink}.
        </p>
      </DialogBox>
    </Modal>
  )
}
